<template>
    <div class="box">
        <div class="box-header with-border">
            <h3 class="box-title">Danh sách user</h3>
            <div class="row search">
                <div class="col-lg-3 col-md-3 col-sm-3 col-xs-3">
                    <Select2 v-model="status" :options="optionStatus" @change="changeStatus($event)" />
                </div>
                <div class="col-lg-3 col-md-3 col-sm-3 col-xs-3">
                    <Select2 v-model="group" :options="optionGroup" @change="changeGroup($event)" />
                </div>
                <div class="col-lg-3 col-md-3 col-sm-3 col-xs-3">
                    <input v-on:keyup="search" type="text" class="form-control" placeholder="Tìm kiếm...">                    
                </div>
            </div>
        </div>
        <div class="box-body">
            <table class="table table-hover" id="table-banner">
                <tr>
                    <th>Username</th>
                    <th style="width:80px;">Avata</th>
                    <th>Số dư</th>
                    <th>Fullname</th>
                    <th>Phone</th>
                    <th>Facebook</th>
                    <th>Zalo</th>
                    <th>Email</th>
                    <th>Store</th>
                    <th>Group</th>
                    <th>Hết hạn</th>
                    <th style="width:75px;">Trạng thái</th>
                    <th style="width:107px;">Chức năng</th>
                </tr>
                <tr v-for="item in user" :key="item.id">
                    <td>{{ item.username }}</td>
                    <td>
                        <img width="80px" height="80px" v-bind:src="item.avata ? api+'/image/avata/'+item.avata : '/tizi/img/avata-default.jpg'" style="object-fit: cover;">
                    </td>
                    <td>{{ number_format(item.account_balance,0) }} đ</td>
                    <td>{{ item.fullname }}</td>
                    <td>{{ item.phone }}</td>
                    <td><a :href="item.facebook" target="_blank">{{ item.facebook }}</a></td>
                    <td>{{ item.zalo }}</td>
                    <td>{{ item.email }}</td>
                    <td>{{ item.name_store }}</td>
                    <td>{{ item.group_name }}</td>
                    <td>{{ item.time_out ? getTime(item.time_out).format("HH:MM DD/MM/YYYY") : '' }}</td>
                    <td>
                        <span v-if="item.status">Đã xác thực</span>
                        <span v-else-if="item.status==2" style="color:red;">Bị khoá</span>
                        <span v-else style="color: #f39c12;">Chưa xác thực</span>
                    </td>
                    <td>
                        <button class="btn btn-warning">Block</button>
                        <button class="btn btn-danger">Xoá</button>
                    </td>
                </tr>
            </table>
            <paginate v-model="page"
                :page-count="pageCount"
                :page-range="3"
                :margin-pages="2"                                           
                :click-handler="runPage"
                :prev-text="`<i class='fa fa-angle-left' aria-hidden='true'></i>`"
                :next-text="`<i class='fa fa-angle-right' aria-hidden='true'></i>`"
                :container-class="'pagination'"
                :page-class="'page-item'">
            </paginate>
        </div>
        <!-- /.box-body -->
    </div>
</template>

<script>
import axios from "axios";
import paginate from "vuejs-paginate";
import Select2 from 'v-select2-component';
import moment from 'moment';
moment.locale("vi");

export default {
    components:{paginate, Select2},
    data() {
        return {
            api: this.$store.state.domainApi,
            status:'',
            optionStatus: [
                {id:'', text: 'Tất cả trạng thái'},
                {id: 0, text: 'Chưa xác thực'},
                {id: 1, text: 'Đã xác thực'},
                {id: 2, text: 'Bị khoá'}
            ],
            group:'',
            optionGroup: [
                {id:'', text: 'Tất cả loại tài khoản'},
                {id: 1, text: 'Thường'},
                {id: 2, text: 'Admin'},
                {id: 3, text: 'Chuyên'},
            ],
            page: 1,
            pageCount:0,
            pageSize: 20,
            user: []
        }
    },
    methods: {
        getDataUser(search=false){
            axios.get(this.api + "/admin/user",{
                headers:{
                    Authorization: `Bearer ${localStorage.token}`
                },
                params:{
                    search: search ? search : '',
                    status: this.status,
                    group: this.group,
                    page: this.page,
                    pageSize: this.pageSize
                }
            })
            .then(response=>{
                this.user = response.data.user;
                this.pageCount = Math.ceil(response.data.count/this.pageSize);
            })
        },
        search(e){
            this.getDataUser(e.target.value);
        },
        changeStatus(){
            this.getDataUser();
        },
        changeGroup(){
            this.getDataUser();
        },
        runPage(page){
            this.page = page;
            this.getDataUser();
        },
        getTime(date){
            var time = new moment(date);
            return time;
        },
        number_format( number, decimals, dec_point, thousands_sep ) {
            var n = number, c = isNaN(decimals = Math.abs(decimals)) ? 2 : decimals;
            var d = dec_point == undefined ? "," : dec_point;
            var t = thousands_sep == undefined ? "." : thousands_sep, s = n < 0 ? "-" : "";
            var i = parseInt(n = Math.abs(+n || 0).toFixed(c)) + "", j = (j = i.length) > 3 ? j % 3 : 0;
                                    
            return s + (j ? i.substr(0, j) + t : "") + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + t) + (c ? d + Math.abs(n - i).toFixed(c).slice(2) : "");
        }
    },
    created() {
        this.getDataUser();
    }
}
</script>

<style scoped>
#table-banner th{
    border: 1px solid #ecf0f5;
    padding: 5px;
}
#table-banner td{
    border: 1px solid #ecf0f5;
    padding: 5px;
}
</style>